#cb-se_modal a.cb-se-remove.cb-on:after, #cb-se_modal a.cb-se-att-remove.cb-on:after {
  content: "\2716"; }

#cb-se_modal a.cb-se-remove.cb-off:after {
  content: "\270E"; }

#cb-se_modal #cb-se-form {
  margin-top: 1em; }

#cb-se_modal .cb-ex {
  float: right;
  font-size: smaller; }

#cb-se_modal .cb-se-atts {
  padding-left: 28px; }

#cb-se_modal .cb-se-addatt {
  cursor: pointer;
  color: black; }

#cb-se_modal .cb-se-addatt.cb-disabled {
  cursor: not-allowed;
  color: darkgrey; }
